import {Component, OnInit} from '@angular/core';
import {RequestsService} from '../../../services/requests.service';
import {ActivatedRoute, Router} from '@angular/router';
import {APUtilService} from "../../../services/ap.util.service";
import {PermissionsService} from "../../../services/permissions.service";
import {Title} from "@angular/platform-browser";

@Component({
    selector: 'admin-detail-component',
    template: require('../../../templates/dashboard/admin/admin-permissions.template.html'),
    styles: []
})
export class AdminPermissionsComponent implements OnInit {

    constructor(private requestsService: RequestsService,
                private router: Router,
                private activatedRoute: ActivatedRoute,
                private apUtilServer: APUtilService,
                private permissionsService: PermissionsService,
                private titleService: Title) {
    };

    id: number;
    firstName: string;
    lastName: string;
    adminPermissions: any;
    permissions: any;

    ngOnInit() {
        if (window.localStorage.getItem('access_token')) {
            if (!this.permissionsService.hasPermission('manage_permissions')) {
                this.router.navigate(['/dashboard/404-not-found/']);
            }

            this.requestsService.getRequest(
                '/permission/'
                , {})
                .subscribe(
                    (response: Response) => {
                        if (response['responseCode'] === 'PER_SUC_01') {
                            //console.log(response['responseData']);
                            this.permissions = response['responseData'];
                            this.activatedRoute.params.subscribe(
                                params => {
                                    this.id = params['id'];
                                    this.requestsService.getRequest(
                                        '/admin/' + this.id, {}
                                    ).subscribe(
                                        response => {
                                            //console.log(response);
                                            if (response['responseCode'] === 'ADM_SUC_08') {
                                                //console.log(response['responseData']);
                                                this.firstName = response['responseData']['contact'].firstName;
                                                this.lastName = response['responseData']['contact'].lastName;
                                                this.titleService.setTitle(this.firstName + ' ' + this.lastName + ' - Permissions')
                                            }
                                        },
                                        (error: any) => {
                                            this.apUtilServer.tokenExpired(error.json()['error']);
                                        });

                                    this.requestsService.getRequest(
                                        '/admin/permission/?adminId=' + this.id, {}
                                    ).subscribe(
                                        response => {
                                            //console.log(response);
                                            if (response['responseCode'] === 'ADM_PER_SUC_01') {
                                                //console.log(response['responseData']);
                                                this.adminPermissions = response['responseData'];
                                                for (let ap of this.adminPermissions) {
                                                    document.getElementById('checkbox-' + ap.id).setAttribute('checked', 'true');
                                                }
                                            }
                                        },
                                        (error: any) => {
                                            this.apUtilServer.tokenExpired(error.json()['error']);
                                            this.router.navigate(['/login/']);
                                        });
                                });
                        }
                    },
                    (error: any) => {
                        this.apUtilServer.tokenExpired(error.json()['error']);
                    }
                );
            if (!this.titleService.getTitle()) {
                this.titleService.setTitle('Admin Permissions');
            }
        }
    }
}



