import {Component, OnInit} from '@angular/core';
import {RequestsService} from '../../../../services/requests.service';
import {ActivatedRoute, Router} from '@angular/router';
import {APUtilService} from "../../../../services/ap.util.service";
import {Title} from "@angular/platform-browser";

@Component({
    selector: 'customer-al-detail-component',
    template: require('../../../../templates/dashboard/customer/activitylog/customer-al-detail.template.html'),
    styles: []
})
export class CustomerActivityLogDetailComponent implements OnInit {

    constructor(private requestsService: RequestsService,
                private router: Router,
                private activatedRoute: ActivatedRoute,
                private apUtilServer: APUtilService,
                private titleService: Title) {
    };

    logId: number;
    userId: number;
    firstName: string;
    lastName: string;
    logName: string = "";
    osVer: string = "";
    browserVer: string = "";
    status: string = "";
    genericError: any;
    genericSuccess: any;
    lastModified: number;
    ipAddress: string;
    sessionId: string;
    macAddress: string;
    latitude: string;
    longitude: string
    city: string;
    country: string;
    formattedLogCreatedOn: string;
    logMessage: string;
    deviceType: string;
    deviceUID: string;
    deviceName: string;
    isDeviceBlocked: string;
    deviceOSVersion: string;

    ngOnInit() {
        if (window.localStorage.getItem('access_token')) {

            this.activatedRoute.params.subscribe(
                params => {
                    this.logId = params['logId'];
                    this.requestsService.getRequest(
                        '/client/activitylog/' + this.logId, {}
                    ).subscribe(
                        response => {
                            //console.log(response);
                            if (response['responseCode'] === 'CLI_AL_SUC_01') {
                                this.userId = response['responseData'].userId;
                                this.firstName = response['responseData'].userFirstName;
                                this.lastName = response['responseData'].userLastName;
                                this.logName = response['responseData'].logType;
                                this.lastModified = response['responseData'].lastModified;
                                this.ipAddress = response['responseData'].ipAddress;
                                this.macAddress = response['responseData'].macAddress;
                                this.sessionId = response['responseData'].sessionId;
                                this.latitude = response['responseData'].latitude;
                                this.longitude = response['responseData'].longitude;
                                this.city = response['responseData'].city;
                                this.country = response['responseData'].country;
                                this.formattedLogCreatedOn = response['responseData'].formattedLogCreatedOn;
                                this.logMessage = response['responseData'].logMessage;
                                this.deviceType = response['responseData'].deviceType;
                                this.deviceUID = response['responseData'].deviceUID;
                                this.deviceName = response['responseData'].deviceName;
                                this.isDeviceBlocked = response['responseData'].isDeviceBlocked;
                                this.deviceOSVersion = response['responseData'].deviceOSVersion;

                                this.titleService.setTitle(this.logId + " - " + 'ActivityLog');
                            } else if (response['responseCode'] === 'CLI_AL_ERR_01') {
                                this.router.navigate(['/dashboard/customer/404-not-found']);
                            }
                        },
                        (error: any) => {
                            this.apUtilServer.tokenExpired(error.json()['error']);
                            this.router.navigate(['/login/']);
                        })
                });
            if (!this.titleService.getTitle()) {
                this.titleService.setTitle('Customer ActivityLog Detail');
            }
        }
    }
}



