import {Component, OnInit} from '@angular/core';
import {RequestsService} from '../../../services/requests.service';
import {Router, ActivatedRoute} from '@angular/router';
import {APUtilService} from "../../../services/ap.util.service";
import {NgForm} from "@angular/forms";
import * as _ from "lodash";
import {Title} from "@angular/platform-browser";

@Component({
    selector: 'customer-policies-component',
    template: require('../../../templates/dashboard/customer/customer-policies.template.html'),
    styles: []
})
export class CustomerPoliciesComponent implements OnInit {

    constructor(private requestsService: RequestsService,
                private router: Router,
                private activatedRoute: ActivatedRoute,
                private apUtilServer: APUtilService,
                private titleService: Title) {
    };

    id: number;
    firstName: string;
    lastName: string;
    genericError: any;
    genericSuccess: any;
    data_array: any[];
    min_char_len: string;
    min_dig_len: string;
    min_pass_len: string;
    max_pass_len: string;
    min_lower_len: string;
    min_upper_len: string;
    min_special_char_len: string;
    failed_attempts: string;
    multi_sign_in: string;
    compare_previous_password: string;
    expiryDay: string;

    ngOnInit() {
        if (window.localStorage.getItem('access_token')) {
            this.activatedRoute.params.subscribe(
                params => {
                    this.id = params['id'];
                    this.requestsService.getRequest(
                        '/client/passwordPolicy/policies/' + this.id, {}
                    ).subscribe(
                        response => {
                            //console.log(response);
                            console.log(response['responseData']);
                            if (response['responseCode'] === 'USR_POL_SUC_01') {
                                this.data_array = response['responseData'];
                                for (var i = 0; i < this.data_array.length; i++) {
                                    if (this.data_array[i].name === 'MIN_CHARACTER_LENGTH') {
                                        this.min_char_len = this.data_array[i].userPolicy;
                                    }
                                    else if (this.data_array[i].name === 'MINIMUM_PASSWORD_LENGTH') {
                                        this.min_pass_len = this.data_array[i].userPolicy;
                                    }
                                    else if (this.data_array[i].name === 'MAXIMUM_PASSWORD_LENGTH') {
                                        this.max_pass_len = this.data_array[i].userPolicy;
                                    }
                                    else if (this.data_array[i].name === 'MINIMUM_DIGITS') {
                                        this.min_dig_len = this.data_array[i].userPolicy;

                                    }
                                    else if (this.data_array[i].name === 'MINIMUM_LOWERCASE') {
                                        this.min_lower_len = this.data_array[i].userPolicy;
                                    }
                                    else if (this.data_array[i].name === 'MINIMUM_UPPERCASE') {
                                        this.min_upper_len = this.data_array[i].userPolicy;
                                    }
                                    else if (this.data_array[i].name === 'MINIMUM_SPECIAL_CHARACTER') {
                                        this.min_special_char_len = this.data_array[i].userPolicy;

                                    }
                                    else if (this.data_array[i].name === 'FAILED_ATTEMPTS') {
                                        this.failed_attempts = this.data_array[i].userPolicy;
                                    }
                                    else if (this.data_array[i].name === 'MULTI_SIGN_IN') {
                                        this.multi_sign_in = this.data_array[i].userPolicy;
                                    }
                                    else if (this.data_array[i].name === 'COMPARE_PREVIOUS_PASSWORD') {
                                        this.compare_previous_password = this.data_array[i].userPolicy;

                                    } else if (this.data_array[i].name === 'EXPIRE_DAYS') {
                                        this.expiryDay = this.data_array[i].userPolicy;
                                    }
                                }
                            } else {
                                this.genericError = response['responseMessage'];
                            }
                        },
                        (error: any) => {
                            this.apUtilServer.tokenExpired(error.json()['error']);
                            //this.genericError = error.json()['error'];
                        });

                    this.requestsService.getRequest(
                        '/client/' + this.id, {}
                    ).subscribe(
                        response => {
                            //console.log(response);
                            console.log(response['responseData']);
                            if (response['responseCode'] === 'CLI_SUC_08') {
                                this.firstName = response['responseData'].firstName;
                                this.lastName = response['responseData'].lastName;

                                this.titleService.setTitle(this.firstName + ' ' + this.lastName + ' - policies');
                            } else {
                                this.genericError = response['responseMessage'];
                            }
                        },
                        (error: any) => {
                            this.apUtilServer.tokenExpired(error.json()['error']);
                            //this.genericError = error.json()['error'];
                        });
                });
            if (!this.titleService.getTitle()) {
                this.titleService.setTitle('Customer Policies');
            }
        }
    }

    updateUserPolicies(form: NgForm) {
        //console.log("Password Policy updateUserPasswordPolicy");
        this.genericSuccess = null;
        this.genericError = null;
        if (window.localStorage.getItem('access_token')) {
            _.each(form.form.controls, function (control) {
                control['_touched'] = true;
            })
            if (form.valid) {
                if (this.min_pass_len === null || this.max_pass_len === null || this.min_dig_len === null || this.min_char_len === null || this.min_lower_len === null || this.min_upper_len === null || this.min_special_char_len === null || this.failed_attempts === null || this.expiryDay === null) {
                    this.genericError = 'Value cannot be null';
                    return;
                }
                else if (Number(this.min_pass_len) < 0 || Number(this.max_pass_len) < 0 || Number(this.min_dig_len) < 0 || Number(this.min_char_len) < 0 || Number(this.min_lower_len) < 0 || Number(this.min_upper_len) < 0 || Number(this.min_special_char_len) < 0 || Number(this.failed_attempts) < 0) {
                    this.genericError = 'Value cannot be negative';
                    return;
                }
                else if (Number(this.min_pass_len) > Number(this.max_pass_len) || Number(this.min_pass_len) === Number(this.max_pass_len)) {
                    this.genericError = 'Minimum Password Length should be less than Maximum Password Length';
                    return;
                } else if (Number(this.min_char_len) != Number(this.min_lower_len) + Number(this.min_upper_len)) {
                    this.genericError = 'Sum of upper and lowercase letters should be equal to minimum letters';
                    return;
                }
                else if (Number(this.max_pass_len) < Number(this.min_char_len) + Number(this.min_dig_len) + Number(this.min_special_char_len)) {
                    this.genericError = 'Sum of letters, digits and special characters should be equal to maximum password length';
                    return;
                }
                this.requestsService.putRequest(
                    '/client/passwordPolicy/updateUserPolicies/' + this.id
                    , {
                        'minPassLength': this.min_pass_len,
                        'maxPassLength': this.max_pass_len,
                        'minDigits': this.min_dig_len,
                        'minLetters': this.min_char_len,
                        'minLower': this.min_lower_len,
                        'minUpper': this.min_upper_len,
                        'minSpecialChar': this.min_special_char_len,
                        'failedAttempts': this.failed_attempts,
                        'multiSignIn': this.multi_sign_in,
                        'comparePreviousPassword': this.compare_previous_password,
                        'expireDays': this.expiryDay

                    })
                    .subscribe(
                        (response: Response) => {
                            if (response['responseCode'] === 'USR_POL_UP_SUC_01') {
                                this.genericSuccess = 'User policies Updated Successfully';
                            } else {
                                this.genericError = 'Error in Update';
                            }
                        },
                        (error: any) => {
                            this.genericError = error;
                        }
                    );

            }
        }
    }

}



