import {Component, OnInit} from '@angular/core';
import {RequestsService} from '../../../../services/requests.service';
import {Router, ActivatedRoute} from '@angular/router';
import {APUtilService} from "../../../../services/ap.util.service";
import * as _ from "lodash";
import {NgForm} from "@angular/forms";
import * as FileSaver from 'file-saver';
import {Title} from "@angular/platform-browser";

@Component({
    selector: 'customer-activitylogs-component',
    template: require('../../../../templates/dashboard/customer/activitylog/customer-activitylogs.template.html'),
    styles: []
})
export class CustomerActivityLogsComponent implements OnInit {

    constructor(private requestsService: RequestsService,
                private router: Router,
                private activatedRoute: ActivatedRoute,
                private apUtilServer: APUtilService,
                private titleService: Title) {
    };

    id: number;
    firstName: string;
    lastName: string;
    searchInputValue: string;
    logName: string = "";
    osVer: string = "";
    browserVer: string = "";
    status: string = "";
    nextPage: any;
    prePage: any;
    currPage: any;
    pages: number[] = [];
    data: any;
    genericError: any;
    genericSuccess: any;
    isTableView: boolean = true;

    ngOnInit() {
        if (window.localStorage.getItem('access_token')) {
            this.getCustomerActivityLogFromServer(0);
        }
    }

    getPageWiseCustomerActivityLogs(page: number) {
        this.getCustomerActivityLogFromServer(page);
        //console.log(page);
    }

    getCustomerActivityLogFromServer(page: number) {
        if (page > 0) {
            page = page;
        }
        this.activatedRoute.params.subscribe(
            params => {
                this.id = params['id'];

                this.requestsService.getRequest(
                    '/client/' + this.id, {}
                ).subscribe(
                    response => {
                        //console.log(response);
                        if (response['responseCode'] === 'CLI_SUC_08') {
                            this.firstName = response['responseData'].firstName;
                            this.lastName = response['responseData'].lastName;

                            this.titleService.setTitle(this.firstName + ' ' + this.lastName + ' - ActivityLogs');

                            this.requestsService.getRequest(
                                '/client/activitylog/' + this.id + '/' + page, {}
                            ).subscribe(
                                response => {
                                    //console.log(response);
                                    if (response['responseCode'] === 'CLI_AL_SUC_01') {
                                        this.nextPage = response['responseData']['nextPage'];
                                        this.prePage = response['responseData']['prePage'];
                                        this.currPage = response['responseData']['currPage'];
                                        this.pages = response['responseData']['pages'];
                                        this.data = response['responseData']['data']
                                    }
                                },
                                (error: any) => {
                                    this.apUtilServer.tokenExpired(error.json()['error']);
                                    this.router.navigate(['/login/']);
                                });
                        } else if (response['responseCode'] === 'CLI_ERR_08') {
                            this.router.navigate(['/dashboard/customer/404-not-found']);
                        }

                    },
                    (error: any) => {
                        this.apUtilServer.tokenExpired(error.json()['error']);
                        this.router.navigate(['/login/']);
                    });
            });
        if (!this.titleService.getTitle()) {
            this.titleService.setTitle('Customer ActivityLogs');
        }
    }

    refreshCustomersActivityLogsTable(page: number) {
        this.getCustomerActivityLogFromServer(page)
    }

    searchCustomerActivityLogs(form: NgForm) {
        _.each(form.form.controls, function (control) {
            control['_touched'] = true
        })

        this.requestsService.getRequest(
            "/client/activitylog/" + this.id + '/search/0?logName=' + this.logName + '&os=' + this.osVer +
            '&browser=' + this.browserVer + '&status=' + this.status, {})
            .subscribe(
                (response: Response) => {
                    if (response['responseCode'] === 'CLI_AL_SUC_01') {
                        this.nextPage = response['responseData']['nextPage'];
                        this.prePage = response['responseData']['prePage'];
                        this.currPage = response['responseData']['currPage'];
                        this.pages = response['responseData']['pages'];
                        this.data = response['responseData']['data']

                        //console.log(response['responseData']);
                    } else if (response['responseCode'] === 'CLI_AL_ERR_01') {
                        this.nextPage = null;
                        this.prePage = null;
                        this.currPage = 0;
                        this.pages = [0];
                        this.data = null;
                    }
                },
                (error: any) => {
                    this.apUtilServer.tokenExpired(error.json()['error']);
                    //console.log(error.json())
                }
            );
    }

    onEnterSearch() {

        this.requestsService.getRequest(
            "/client/activitylog/" + this.id + '/search/0?logName=' + this.searchInputValue + '&os=' + this.searchInputValue +
            '&browser=' + this.searchInputValue + '&status=' + this.searchInputValue + '&isFormSearch=false', {})
            .subscribe(
                (response: Response) => {
                    if (response['responseCode'] === 'CLI_AL_SUC_01') {
                        this.nextPage = response['responseData']['nextPage'];
                        this.prePage = response['responseData']['prePage'];
                        this.currPage = response['responseData']['currPage'];
                        this.pages = response['responseData']['pages'];
                        this.data = response['responseData']['data']

                        //console.log(response['responseData']);
                    } else if (response['responseCode'] === 'CLI_AL_ERR_01') {
                        this.nextPage = null;
                        this.prePage = null;
                        this.currPage = 0;
                        this.pages = [0];
                        this.data = null;
                    }
                },
                (error: any) => {
                    this.apUtilServer.tokenExpired(error.json()['error']);
                    //console.log(error.json())
                }
            );
    }

    generateActivityLogsPdfReport() {
        this.requestsService.getRequestReport(
            "/client/report/activityLog/" + this.id, {})
            .subscribe(
                data => {
                    //console.log(data);
                    var blob = new Blob([data], {type: 'application/pdf'});
                    FileSaver.saveAs(blob, "Client ActivityLog Report.pdf");
                },
                (error: any) => {
                    this.apUtilServer.tokenExpired(error.json()['error']);
                    //console.log(error)
                }
            );
    }

    viewChange(tableView: boolean) {
        this.isTableView = tableView;
        this.getCustomerActivityLogFromServer(0);
    }
}



