import {Component, OnInit} from '@angular/core';
import {RequestsService} from '../../../services/requests.service';
import {Router, ActivatedRoute} from '@angular/router';
import {APUtilService} from "../../../services/ap.util.service";
import * as _ from "lodash";
import {NgForm} from "@angular/forms";
import * as FileSaver from 'file-saver';
import {Title} from "@angular/platform-browser";

@Component({
    selector: 'customer-gadget-component',
    template: require('../../../templates/dashboard/customer/customer-gadget.template.html'),
    styles: []
})
export class CustomerGadgetComponent implements OnInit {

    constructor(private requestsService: RequestsService,
                private router: Router,
                private activatedRoute: ActivatedRoute,
                private apUtilServer: APUtilService,
                private titleService: Title) {
    };

    id: number;
    firstName: string;
    lastName: string;
    searchInputValue: string;
    deviceName: string = "";
    deviceType: string = "";
    uid: string = "";
    nextPage: any;
    prePage: any;
    currPage: any;
    pages: number[] = [];
    data: any;
    genericError: any;
    genericSuccess: any;

    ngOnInit() {
        if (window.localStorage.getItem('access_token')) {
            this.getCustomerGadgetFromServer(0);
            if (!this.titleService.getTitle()) {
                this.titleService.setTitle('Customer Gadgets');
            }
        }
    }

    getPageWiseCustomerGadget(page: number) {
        this.getCustomerGadgetFromServer(page);
        //console.log(page);
    }

    getCustomerGadgetFromServer(page: number) {
        if (page > 0) {
            page = page;
        }
        this.activatedRoute.params.subscribe(
            params => {
                this.id = params['id'];

                this.requestsService.getRequest(
                    '/client/' + this.id, {}
                ).subscribe(
                    response => {
                        //console.log(response);
                        if (response['responseCode'] === 'CLI_SUC_08') {
                            this.firstName = response['responseData'].firstName;
                            this.lastName = response['responseData'].lastName;
                            this.titleService.setTitle(this.firstName + " " + this.lastName + " - Gadgets")
                        }
                    },
                    (error: any) => {
                        this.apUtilServer.tokenExpired(error.json()['error']);
                        this.router.navigate(['/login/']);
                    })

                this.requestsService.getRequest(
                    '/client/' + this.id + "/device/" + page, {}
                ).subscribe(
                    response => {
                        //console.log(response);
                        if (response['responseCode'] === 'CLI_DEV_SUC_02') {
                            this.nextPage = response['responseData']['nextPage'];
                            this.prePage = response['responseData']['prePage'];
                            this.currPage = response['responseData']['currPage'];
                            this.pages = response['responseData']['pages'];
                            this.data = response['responseData']['data']
                        }
                    },
                    (error: any) => {
                        this.apUtilServer.tokenExpired(error.json()['error']);
                        this.router.navigate(['/login/']);
                    })
            })
    }

    refreshCustomersGadgetsTable(page: number) {
        this.getCustomerGadgetFromServer(page)
    }

    searchCustomerGadgets(form: NgForm) {
        _.each(form.form.controls, function (control) {
            control['_touched'] = true
        })

        this.requestsService.getRequest(
            "/client/" + this.id + '/search/device/0?deviceName=' + this.deviceName + '&deviceType=' + this.deviceType +
            '&uid=' + this.uid, {})
            .subscribe(
                (response: Response) => {
                    if (response['responseCode'] === 'CLI_DEV_SUC_02') {
                        this.nextPage = response['responseData']['nextPage'];
                        this.prePage = response['responseData']['prePage'];
                        this.currPage = response['responseData']['currPage'];
                        this.pages = response['responseData']['pages'];
                        this.data = response['responseData']['data']

                        //console.log(response['responseData']);
                    } else if (response['responseCode'] === 'CLI_DEV_ERR_03') {
                        this.nextPage = null;
                        this.prePage = null;
                        this.currPage = 0;
                        this.pages = [0];
                        this.data = null;
                    }
                },
                (error: any) => {
                    this.apUtilServer.tokenExpired(error.json()['error']);
                    //console.log(error.json())
                }
            );
    }

    onEnterSearch() {

        this.requestsService.getRequest(
            "/client/" + this.id + '/search/device/0?deviceName=' + this.searchInputValue + '&deviceType=' + "" +
            '&uid=' + "", {})
            .subscribe(
                (response: Response) => {
                    if (response['responseCode'] === 'CLI_DEV_SUC_02') {
                        this.nextPage = response['responseData']['nextPage'];
                        this.prePage = response['responseData']['prePage'];
                        this.currPage = response['responseData']['currPage'];
                        this.pages = response['responseData']['pages'];
                        this.data = response['responseData']['data']

                        //console.log(response['responseData']);
                    } else if (response['responseCode'] === 'CLI_DEV_ERR_03') {
                        this.nextPage = null;
                        this.prePage = null;
                        this.currPage = 0;
                        this.pages = [0];
                        this.data = null;
                    }
                },
                (error: any) => {
                    this.apUtilServer.tokenExpired(error.json()['error']);
                    //console.log(error.json())
                }
            );
    }

    suspendCustomerGadget(clientId: number, deviceUID: string, index: number) {

        if (clientId && deviceUID) {
            this.requestsService.putRequest(
                '/admin/client/' + clientId + "/suspend/device/" + deviceUID
                , {})
                .subscribe(
                    (response: Response) => {
                        if (response['responseCode'] === 'CLI_DEV_SUC_03') {
                            this.genericSuccess = response['responseMessage'];
                            this.data[index] = response['responseData'];
                            this.getCustomerGadgetFromServer(this.currPage);
                        } else {
                            this.genericError = response['responseMessage'];
                        }
                    },
                    (error: any) => {
                        //console.log(error.json())
                        this.genericError = error.json()['responseMessage'];
                        this.apUtilServer.tokenExpired(error.json()['error']);
                    }
                );
        }
    }

    unSuspendCustomerGadget(clientId: number, deviceUID: string, index: number) {

        if (clientId && deviceUID) {
            this.requestsService.putRequest(
                '/admin/client/' + clientId + "/unsuspend/device/" + deviceUID
                , {})
                .subscribe(
                    (response: Response) => {
                        if (response['responseCode'] === 'CLI_DEV_SUC_04') {
                            this.genericSuccess = response['responseMessage'];
                            this.data[index] = response['responseData'];
                            this.getCustomerGadgetFromServer(this.currPage);
                        } else {
                            this.genericError = response['responseMessage'];
                        }
                    },
                    (error: any) => {
                        //console.log(error.json())
                        this.genericError = error.json()['responseMessage'];
                        this.apUtilServer.tokenExpired(error.json()['error']);
                    }
                );
        }
    }

    generateClientsPdfReport() {
        this.requestsService.getRequestReport(
            "/admin/report/devices/" + this.id, {})
            .subscribe(
                data => {
                    //console.log(data);
                    var blob = new Blob([data], {type: 'application/pdf'});
                    FileSaver.saveAs(blob, "Client LoggedIn Devices Report.pdf");
                },
                (error: any) => {

                    this.apUtilServer.tokenExpired(error.json()['error']);
                    //console.log(error)
                }
            );
    }
}



