import {Component, OnInit} from '@angular/core';
import {Router} from '@angular/router';
import {RequestsService} from '../../../services/requests.service';
import {CountryService} from './../../../services/country.service';
import {CountryComponent} from './../../miscellaneous/country.component';
import {CityComponent} from './../../miscellaneous/city.component';
import {IMyDpOptions, MyDatePicker} from 'mydatepicker';
import {NgForm} from "@angular/forms";
import {PermissionsService} from "../../../services/permissions.service";
import * as copy from 'copy-to-clipboard';
import {Title} from "@angular/platform-browser";

@Component({
    selector: 'admin-add-component',
    template: require('../../../templates/dashboard/admin/admin-add.template.html'),
    providers: [CountryService]
})
export class AdminAddComponent implements OnInit {

    constructor(private requestsService: RequestsService,
                private router: Router,
                private _countryService: CountryService,
                private permissionsService: PermissionsService,
                private titleService: Title) {

        this.selectedCountry = new CountryComponent('Select', 'Select Country');
        this.selectedCity = new CityComponent('Select', 'Select Country', 'Select City');
        this.countries = this._countryService.getCountries();
    }

    private myDatePickerOptions: IMyDpOptions = {
        dateFormat: 'dd.mm.yyyy',
    };

    genericSuccess: any;
    genericError: any;
    userName: String;
    email: String;
    userRole: string = 'Admin';
    password: String;
    firstName: String;
    lastName: String;
    phoneNo: number;
    gender: string;
    city: String;
    state: String;
    address: String;
    confirmPassword: String;
    dob: MyDatePicker;
    url: string;

    selectedCountry: CountryComponent;
    selectedCity: CityComponent;
    countries: CountryComponent[];
    cities: CityComponent[];
    id: number;

    data_array: any[];
    min_char_len: string;
    min_dig_len: string;
    min_pass_len: string;
    max_pass_len: string;
    min_lower_len: string;
    min_upper_len: string;
    min_special_char_len: string;
    failed_attempts: string;
    samplePassword: string;

    ngOnInit() {
        if (window.localStorage.getItem('access_token')) {
            this.titleService.setTitle('Create Admin');
            if (!this.permissionsService.hasPermission('create_admin')) {
                this.router.navigate(['/dashboard/404-not-found/']);
            }
            this.requestsService.getRequest(
                '/policy/password/policies/'
                , {})
                .subscribe(
                    (response: Response) => {
                        if (response['responseCode'] === 'PASS_POL_SUC_01') {
                            //console.log(response);
                            this.data_array = response['responseData'];
                            for (var i = 0; i < this.data_array.length; i++) {
                                if (this.data_array[i].name === 'MIN_CHARACTER_LENGTH') {
                                    this.min_char_len = this.data_array[i].policy;

                                } else if (this.data_array[i].name === 'MINIMUM_PASSWORD_LENGTH') {
                                    this.min_pass_len = this.data_array[i].policy;

                                } else if (this.data_array[i].name === 'MAXIMUM_PASSWORD_LENGTH') {
                                    this.max_pass_len = this.data_array[i].policy;

                                } else if (this.data_array[i].name === 'MINIMUM_DIGITS') {
                                    this.min_dig_len = this.data_array[i].policy;

                                } else if (this.data_array[i].name === 'MINIMUM_LOWERCASE') {
                                    this.min_lower_len = this.data_array[i].policy;

                                } else if (this.data_array[i].name === 'MINIMUM_UPPERCASE') {
                                    this.min_upper_len = this.data_array[i].policy;

                                } else if (this.data_array[i].name === 'MINIMUM_SPECIAL_CHARACTER') {
                                    this.min_special_char_len = this.data_array[i].policy;

                                } else if (this.data_array[i].name === 'SAMPLE_PASSWORD') {
                                    this.samplePassword = this.data_array[i].policy;
                                }
                            }
                        }
                    },
                    (error: any) => {
                        this.genericError = error;
                    }
                );
        }
    }

    onSelect(countryId: string) {
        this.cities = this._countryService.getCities().filter((item) => item.countryid == countryId);
    }

    createAdmin(form: NgForm) {
        console.log("Create Admin");
        if (form.valid) {
            if (this.firstName != null || this.lastName != null || this.userName != null || this.email != null ||
                this.password != null || this.dob['formatted'] != null) {
                if (this.password === this.confirmPassword) {
                    this.requestsService.postRequest(
                        '/admin/create'
                        , {
                            'userName': this.userName,
                            'email': this.email,
                            'roleName': this.userRole,
                            'password': this.password,
                            'lastName': this.lastName,
                            'firstName': this.firstName,
                            'gender': this.gender,
                            'dob': this.dob['formatted'],
                            'phoneNumber': this.phoneNo,
                            'country': this.selectedCountry.id,
                            'city': this.selectedCity.id,
                            'state': this.state,
                            'address': this.address,

                        }).subscribe(
                        (response: Response) => {
                            if (response['responseCode'] === 'ADM_SUC_02') {
                                this.genericError = null;
                                this.genericSuccess = response['responseMessage'];
                                this.router.navigate(['/dashboard/admin'])
                            } else {
                                this.router.navigate(['/login/']);
                                window.localStorage.removeItem('access_token');
                                this.genericError = response['responseMessage'];
                            }
                        },
                        (error: any) => {
                            //console.log(error.json())
                            this.genericError = error.json()['responseMessage'];
                        }
                    );
                } else {
                    this.genericError = "Password & Confirm password does not matched.";
                }
            }
        }
    }

    copyToClipboard() {
        if (copy(this.samplePassword)) {
            this.genericSuccess = "Sample password copied";
        } else {
            this.genericError = "System Error in copying of Sample password.";
        }
    }
}


