import { Component, OnInit } from '@angular/core';
import { Response } from '@angular/http';
import { NgForm } from '@angular/forms';
import { Router, ActivatedRoute } from '@angular/router';
import { WindowService } from '../../../services/window.service';
import { RequestsService } from '../../../services/requests.service';
import * as _ from 'lodash';

@Component({
  selector: 'admin-edit-component',
  template: require('../../../templates/dashboard/admin/admin-add.template.html'),
  styles: [`
    .help-text{
      color: #777;
      font-size: 11px;
      line-height: 30px;

    }
    .help-text-bottom{
      color: #777;
      font-size: 11px;
      line-height: 10px;
      width: 500px;
      z-index: 333
    }
  `],
  providers: [ WindowService, RequestsService ]
})
export class AdminEditComponent implements OnInit{
  constructor (
    private window: WindowService,
    private requestsService: RequestsService,
    private router: Router,
    private activatedRoute: ActivatedRoute){
  };
  title: string = 'Edit Admin(';
  id: any;
  adminName: any;
  adminUsername: any;
  adminEmail: any;
  adminPassword: any;
  adminConfirmPassword: any;
  adminIsSuperadmin: boolean = true;
  passwordMatched: boolean = false;


  cancel(){
    this.router.navigate(['/dashboard/admin/list']);
  }

  navigateAddRole(){
    this.router.navigate(['/dashboard/role/add']);
  }

  onRoleChange() {
    console.log('Handle Role Change');
  }

  saveAdmin(form: NgForm){
    _.each(form.form.controls, function(control){control['_touched']=true});
    if (form.valid){
      this.requestsService.postRequest(
        '/auth/admin/update/' + this.id + '/'
        ,{
          'is_superadmin': this.adminIsSuperadmin,
          "full_name": this.adminName,
          "email": this.adminEmail,
          "username": this.adminUsername,
          "password": this.adminPassword
        }
      ).subscribe(
        (response: Response) => {
          this.router.navigate(['/dashboard/admin/list/']);
        },
        (error: any) => console.log(error),
      );
    }
  }

  onPasswordChange(){
    if(this.adminPassword == this.adminConfirmPassword){
      this.passwordMatched = true
    }
    else{this.passwordMatched = false}
  }

  ngOnInit(){

  }
}


