import {Component} from "@angular/core";
import {NgForm} from "@angular/forms";
import {Response} from "@angular/http";
import {ActivatedRoute, Router} from '@angular/router';
import {RequestsService} from "../services/requests.service";
import {AppConstants} from "../utils/app.constants";
import * as copy from 'copy-to-clipboard';
import {Title} from "@angular/platform-browser";

@Component({
    selector: 'expired-password-component',
    template: require('../templates/expired-change-password.template.html'),
    styles: [],
})
export class ExpiredChangePasswordComponent {
    constructor(private requestsService: RequestsService,
                private router: Router,
                private activatedRoute: ActivatedRoute,
                private titleService: Title) {
    };

    password: string;
    confPassword: string;
    token: string;
    success: any;
    error: any;

    dataArray: any[];
    minCharLen: string;
    minDigLen: string;
    minPassLen: string;
    maxPassLen: string;
    minLowerLen: string;
    minUpperLen: string;
    minSpecialCharLen: string;
    samplePassword: string;
    adminId: string;

    ngOnInit() {
        this.titleService.setTitle('Admin Expire Change Password');

        this.activatedRoute.queryParams.subscribe(
            params => {
                this.token = params['token'];
            });
        //  console.log(atob(this.token));
        //  console.log(atob(this.token).split(':', 2));
        // console.log(atob(this.token).split(':', 2)[0]);
        this.adminId = atob(this.token).split(':', 2)[0];
        if (!this.token) {
            this.router.navigate(['/login/']);
            window.localStorage.removeItem(AppConstants.EXPIRE_PASSWORD_TOKEN);
        }

        if (!window.localStorage.getItem(AppConstants.EXPIRE_PASSWORD_TOKEN)) {
            this.router.navigate(['/login/']);
            if (this.token != window.localStorage.getItem(AppConstants.EXPIRE_PASSWORD_TOKEN)) {
                this.router.navigate(['/login/']);
                window.localStorage.removeItem(AppConstants.EXPIRE_PASSWORD_TOKEN);
            }
        }

        this.requestsService.getRequest(
            '/unAuth/admin/policies/' + Number(this.adminId)
            , {})
            .subscribe(
                (response: Response) => {
                    if (response['responseCode'] === 'USR_POL_SUC_01') {
                        //console.log(response);
                        this.dataArray = response['responseData'];
                        for (var i = 0; i < this.dataArray.length; i++) {
                            if (this.dataArray[i].name === 'MIN_CHARACTER_LENGTH') {
                                this.minCharLen = this.dataArray[i].userPolicy;

                            } else if (this.dataArray[i].name === 'MINIMUM_PASSWORD_LENGTH') {
                                this.minPassLen = this.dataArray[i].userPolicy;

                            } else if (this.dataArray[i].name === 'MAXIMUM_PASSWORD_LENGTH') {
                                this.maxPassLen = this.dataArray[i].userPolicy;

                            } else if (this.dataArray[i].name === 'MINIMUM_DIGITS') {
                                this.minDigLen = this.dataArray[i].userPolicy;

                            } else if (this.dataArray[i].name === 'MINIMUM_LOWERCASE') {
                                this.minLowerLen = this.dataArray[i].userPolicy;

                            } else if (this.dataArray[i].name === 'MINIMUM_UPPERCASE') {
                                this.minUpperLen = this.dataArray[i].userPolicy;

                            } else if (this.dataArray[i].name === 'MINIMUM_SPECIAL_CHARACTER') {
                                this.minSpecialCharLen = this.dataArray[i].userPolicy;

                            } else if (this.dataArray[i].name === 'SAMPLE_PASSWORD') {
                                this.samplePassword = this.dataArray[i].userPolicy;

                            }
                        }
                    }
                },
                (error: any) => {
                    this.error = error;
                }
            );
    }

    onChange() {
        this.error = null;
    }

    changePassword(form: NgForm) {
        if (this.password != this.confPassword) {
            this.error = "Password and Confirm Password don't match.";
            return;
        }
        this.requestsService.postUnAuthRequest(
            '/unAuth/admin/changeExpiredPassword/token?token=' + this.token
            , {
                "password": this.password,
                "confirmPassword": this.confPassword,
            })
            .subscribe(
                (response: Response) => {
                    if (response['responseCode'] === 'ADM_EXP_PASS_SUC_01') {
                        this.success = response['responseMessage'] + " Redirecting to Login Page";
                        setTimeout(() => {
                            window.localStorage.removeItem(AppConstants.EXPIRE_PASSWORD_TOKEN);
                            this.router.navigate(['/login'])
                        }, 5000);
                    } else {
                        window.localStorage.removeItem(AppConstants.EXPIRE_PASSWORD_TOKEN);
                        this.error = response['responseMessage'];
                    }
                },
                (error: any) => {
                    //console.log(error.json())
                    window.localStorage.removeItem(AppConstants.EXPIRE_PASSWORD_TOKEN);
                    this.error = error.json();
                }
            );
    }

    copyToClipboard() {
        if (copy(this.samplePassword)) {
            this.success = "Sample password copied";
        } else {
            this.error = "System Error in copying of Sample password.";
        }
    }

}



