import { Component, OnInit } from '@angular/core';
import { Response } from '@angular/http';
import { NgForm } from '@angular/forms';
import { Router } from '@angular/router';
import { WindowService } from '../../../services/window.service';
import { RequestsService } from '../../../services/requests.service';

@Component({
  selector: 'role-list-component',
  template: require('../../../templates/dashboard/role/role-list.template.html'),
  styles: [`
    .help-text{
      color: #777;
      font-size: 11px;
      line-height: 30px;

    }
    .help-text-bottom{
      color: #777;
      font-size: 11px;
      line-height: 10px;
      width: 500px;
      z-index: 333
    }
    .loader {
      margin-top: 0px !important;
    }
  `],
  providers: [ WindowService, RequestsService ]
})
export class RoleListComponent implements OnInit{
  constructor (
    private window: WindowService,
    private requestsService: RequestsService,
    private router: Router){
  };

  roles: any[] = [];
  noRoles: boolean = false;

  deleteRole(role: any){
    this.requestsService.deleteRequest('/auth/role/delete/' + role.id + '/', {})
      .subscribe(
        response => {
    this.requestsService.getRequest('/auth/role/list/', {

      }).subscribe(
        (response: Response) => {
          this.roles=[]
          for (var i in response){
            this.roles.push(response[i]);
          }
          console.log(response);
        },
        (error: any) => {
          console.log(error);
        }
      )
    },
    (error: any) => {
      console.log(error);
    })
  }

  ngOnInit(){

  }
}


