import { Component, OnInit } from '@angular/core';
import { Response } from '@angular/http';
import { NgForm } from '@angular/forms';
import { Router } from '@angular/router';
import { WindowService } from '../../../services/window.service';
import { RequestsService } from '../../../services/requests.service';

@Component({
  selector: 'admin-list-component',
  template: require('../../../templates/dashboard/admin/admin-list.template.html'),
  styles: [`
    .help-text{
      color: #777;
      font-size: 11px;
      line-height: 30px;

    }
    .help-text-bottom{
      color: #777;
      font-size: 11px;
      line-height: 10px;
      width: 500px;
      z-index: 333
    }
    .loader {
      margin-top: 0px !important;
    }
  `],
  providers: [ WindowService, RequestsService ]
})
export class AdminListComponent implements OnInit{
  constructor (
    private window: WindowService,
    private requestsService: RequestsService,
    private router: Router){
  };
  admins: any[] = [];
  noAdmins: boolean = false;

  deleteAdmin(admin: any){
    this.requestsService.deleteRequest('/auth/admin/delete/' + admin.id + '/', {})
      .subscribe(
        response => {
          this.requestsService.getRequest('/auth/admin/list/', {

          }).subscribe(
            (response: Response) => {
              this.admins=[]
              for (var i in response){
                this.admins.push(response[i]);
              }
              console.log(response);
            },
            (error: any) => {
              console.log(error);
            }
          )
        })
  }

  ngOnInit(){

  }
}


