import {Component, OnInit} from '@angular/core';
import {RequestsService} from '../../../services/requests.service';
import {Response} from '@angular/http';
import {Router} from '@angular/router';
import {NgForm} from "@angular/forms";
import * as _ from "lodash";
import {IMyDpOptions, MyDatePicker} from 'mydatepicker'
import {APUtilService} from "../../../services/ap.util.service";
import * as FileSaver from 'file-saver';
import * as copy from 'copy-to-clipboard';
import {Title} from "@angular/platform-browser";

@Component({
    selector: 'customer-component',
    template: require('../../../templates/dashboard/customer/customers.template.html'),
    styles: [],
})
export class CustomerComponent implements OnInit {

    constructor(private requestsService: RequestsService,
                private router: Router,
                private apUtilServer: APUtilService,
                private titleService: Title) {

    };

    private myDatePickerOptions: IMyDpOptions = {
        // other options...
        dateFormat: 'dd.mm.yyyy',
    };

    nextPage: any;
    prePage: any;
    currPage: any;
    pages: number[] = [];
    data: any;
    fullName: string = "";
    userName: string = "";
    emailAdd: string = "";
    clientId: number;
    newPassw: string;
    confirmPass: string;
    error: any;
    success: any;
    genericError: any;
    genericSuccess: any;
    dateFrom: MyDatePicker;
    dateTo: MyDatePicker;
    searchInputValue: string;

    dataArray: any[];
    minCharLen: string;
    minDigLen: string;
    minPassLen: string;
    maxPassLen: string;
    minLowerLen: string;
    minUpperLen: string;
    minSpecialCharLen: string;
    samplePassword: string;
    isProcessing: boolean = false;
    resetEmail: boolean = true;
    selectedCustomerId: number;
    selectedCustomerFirstName: string;
    selectedCustomerLastName: string;
    selectedCustomerIndex: number;
    editCustomerFirstName: string;
    editCustomerLastName: string;
    editCustomerPhoneNo: string;


    ngOnInit() {
        if (window.localStorage.getItem('access_token')) {
            this.titleService.setTitle('Customers');
            this.getCustomerFromServer(0);
        }
    }

    getPageWiseCustomer(page: number) {
        this.data = null;
        this.getCustomerFromServer(page);
        //console.log(page);
    }

    getCustomerFromServer(page: number) {
        if (page > 0) {
            page = page;
        }
        this.requestsService.getRequest(
            '/client/clients/' + page
            , {})
            .subscribe(
                (response: Response) => {
                    if (response['responseCode'] === 'CLI_SUC_11') {
                        this.nextPage = response['responseData']['nextPage'];
                        this.prePage = response['responseData']['prePage'];
                        this.currPage = response['responseData']['currPage'];
                        this.pages = response['responseData']['pages'];
                        this.data = response['responseData']['data']

                        //console.log(response['responseData']);
                    }
                },
                (error: any) => {
                    this.apUtilServer.tokenExpired(error.json()['error']);
                    //console.log(error.json()['error']);
                }
            );
    }

    refreshCustomersTable(page: number) {
        this.getCustomerFromServer(page)
    }

    searchCustomers(form: NgForm) {
        _.each(form.form.controls, function (control) {
            control['_touched'] = true
        })

        var dateFromMilli = 0, dateToMilli = new Date().getTime();
        if (this.dateFrom != null) {
            dateFromMilli = this.dateFrom['epoc'] * 1000;
        }
        if (this.dateTo != null) {
            dateToMilli = this.dateTo['epoc'] + 86399;
            dateToMilli = dateToMilli * 1000;
        }

        this.requestsService.getRequest(
            '/client/search/0?fullName=' + this.fullName + '&userName=' + this.userName +
            '&email=' + this.emailAdd + '&dateFrom=' + dateFromMilli + '&dateTo=' + dateToMilli
            , {})
            .subscribe(
                (response: Response) => {
                    if (response['responseCode'] === 'CLI_SUC_11') {
                        this.nextPage = response['responseData']['nextPage'];
                        this.prePage = response['responseData']['prePage'];
                        this.currPage = response['responseData']['currPage'];
                        this.pages = response['responseData']['pages'];
                        this.data = response['responseData']['data']

                        //console.log(response['responseData']);
                    } else if (response['responseCode'] === 'CLI_ERR_11') {
                        this.nextPage = null;
                        this.prePage = null;
                        this.currPage = 0;
                        this.pages = [0];
                        this.data = null;
                    }
                },
                (error: any) => {
                    this.apUtilServer.tokenExpired(error.json()['error']);
                    //console.log(error.json())
                }
            );
    }

    onResetPopupLoad(clientId: number) {
        this.clientId = clientId;
        this.error = null;
        this.success = null;
        this.newPassw = null;
        this.confirmPass = null;

        if (window.localStorage.getItem('access_token')) {

            this.requestsService.getRequest(
                '/client/passwordPolicy/policies/' + clientId
                , {})
                .subscribe(
                    (response: Response) => {
                        if (response['responseCode'] === 'USR_POL_SUC_01') {
                            //console.log(response);
                            this.dataArray = response['responseData'];
                            for (var i = 0; i < this.dataArray.length; i++) {
                                if (this.dataArray[i].name === 'MIN_CHARACTER_LENGTH') {
                                    this.minCharLen = this.dataArray[i].userPolicy;

                                } else if (this.dataArray[i].name === 'MINIMUM_PASSWORD_LENGTH') {
                                    this.minPassLen = this.dataArray[i].userPolicy;

                                } else if (this.dataArray[i].name === 'MAXIMUM_PASSWORD_LENGTH') {
                                    this.maxPassLen = this.dataArray[i].userPolicy;

                                } else if (this.dataArray[i].name === 'MINIMUM_DIGITS') {
                                    this.minDigLen = this.dataArray[i].userPolicy;

                                } else if (this.dataArray[i].name === 'MINIMUM_LOWERCASE') {
                                    this.minLowerLen = this.dataArray[i].userPolicy;

                                } else if (this.dataArray[i].name === 'MINIMUM_UPPERCASE') {
                                    this.minUpperLen = this.dataArray[i].userPolicy;

                                } else if (this.dataArray[i].name === 'MINIMUM_SPECIAL_CHARACTER') {
                                    this.minSpecialCharLen = this.dataArray[i].userPolicy;

                                } else if (this.dataArray[i].name === 'SAMPLE_PASSWORD') {
                                    this.samplePassword = this.dataArray[i].userPolicy;

                                }
                            }
                        }
                    },
                    (error: any) => {
                        this.genericError = error;
                    }
                );
        }
    }

    onResetPopupCancel(form: NgForm) {
        _.each(form.form.controls, function (control) {
            control['_touched'] = false;
        })
    }

    resetPassword(form: NgForm, email: string) {
        _.each(form.form.controls, function (control) {
            control['_touched'] = true;
        })
        if (form.valid) {
            this.success = null;
            this.error = null;
            if (this.resetEmail) {
                this.clientResetPassword(email);
            }
            else {
                if (this.newPassw === this.confirmPass) {
                    this.requestsService.putRequest(
                        '/client/auth/resetPassword'
                        , {
                            'clientId': this.clientId,
                            'newPassword': this.newPassw,
                            'confirmPassword': this.confirmPass,
                        })
                        .subscribe(
                            (response: Response) => {
                                if (response['responseCode'] === 'CLI_PASS_SUC_03') {
                                    this.error = null;
                                    this.success = response['responseMessage'];
                                } else {
                                    this.error = response['responseData'];
                                    this.success = null;
                                }
                            },
                            (error: any) => {
                                //console.log(error.json())
                                this.error = error.json()['responseMessage'];
                                this.apUtilServer.tokenExpired(error.json()['error']);
                            }
                        );
                } else {
                    this.error = 'New & Confirm password does not matched';
                }
            }
        }
    }

    suspendClient(clientId: number, index: number) {

        if (clientId) {
            this.requestsService.putRequest(
                '/client/suspend/' + clientId
                , {})
                .subscribe(
                    (response: Response) => {
                        if (response['responseCode'] === 'CLI_SUC_12') {
                            this.genericSuccess = response['responseMessage'];
                            this.data[index] = response['responseData'];
                            this.getCustomerFromServer(this.currPage);
                        } else {
                            this.genericError = response['responseMessage'];
                        }
                    },
                    (error: any) => {
                        //console.log(error.json())
                        this.genericError = error.json()['responseMessage'];
                        this.apUtilServer.tokenExpired(error.json()['error']);
                    }
                );
        }
    }

    unsuspendClient(clientId: number, index: number) {

        if (clientId) {
            this.requestsService.putRequest(
                '/client/unSuspend/' + clientId
                , {})
                .subscribe(
                    (response: Response) => {
                        if (response['responseCode'] === 'CLI_SUC_13') {
                            this.genericSuccess = response['responseMessage'];
                            this.data[index] = response['responseData'];
                        } else {
                            this.genericError = response['responseMessage'];
                        }
                    },
                    (error: any) => {
                        //console.log(error.json())
                        this.genericError = error.json()['responseMessage'];
                        this.apUtilServer.tokenExpired(error.json()['error']);
                    }
                );
        }
    }

    goToCustomerPolicies(clientId: number) {

        if (clientId) {


        } else {
            this.genericError = 'Invalid client selected.';
        }
    }

    onEnterSearch() {
        var regexp = new RegExp(/^\w+@[a-zA-Z_]+?\.[a-zA-Z]{2,3}$/);
        var emailAdd = "", url;
        var searchInputValueEmail = regexp.test(this.searchInputValue);

        if (searchInputValueEmail === true) {
            emailAdd = this.searchInputValue;
            url = '/client/search/0?fullName=' + '' + '&userName=' + '' +
                '&email=' + emailAdd + '&dateFrom=' + 0 + '&dateTo=' + new Date().getTime()

            this.requestsService.getRequest(
                url, {})
                .subscribe(
                    (response: Response) => {
                        if (response['responseCode'] === 'CLI_SUC_11') {
                            this.nextPage = response['responseData']['nextPage'];
                            this.prePage = response['responseData']['prePage'];
                            this.currPage = response['responseData']['currPage'];
                            this.pages = response['responseData']['pages'];
                            this.data = response['responseData']['data'];

                            //console.log(response['responseData']);
                        } else if (response['responseCode'] === 'CLI_ERR_11') {
                            this.nextPage = null;
                            this.prePage = null;
                            this.currPage = 0;
                            this.pages = [0];
                            this.data = null;
                        }
                    },
                    (error: any) => {
                        this.apUtilServer.tokenExpired(error.json()['error']);
                        //console.log(error.json())
                    }
                );

        } else {
            url = '/client/search/0?fullName=' + this.searchInputValue + '&userName=' + '' +
                '&email=' + '' + '&dateFrom=' + 0 + '&dateTo=' + new Date().getTime()

            this.requestsService.getRequest(
                url, {})
                .subscribe(
                    (response: Response) => {
                        if (response['responseCode'] === 'CLI_SUC_11') {
                            this.nextPage = response['responseData']['nextPage'];
                            this.prePage = response['responseData']['prePage'];
                            this.currPage = response['responseData']['currPage'];
                            this.pages = response['responseData']['pages'];
                            this.data = response['responseData']['data'];

                            //console.log(response['responseData']);
                        } else if (response['responseCode'] === 'CLI_ERR_11') {
                            this.nextPage = null;
                            this.prePage = null;
                            this.currPage = 0;
                            this.pages = [0];
                            this.data = null;

                            if (!this.data) {
                                url = '/client/search/0?fullName=' + '' + '&userName=' + this.searchInputValue +
                                    '&email=' + '' + '&dateFrom=' + 0 + '&dateTo=' + new Date().getTime()
                                this.requestsService.getRequest(
                                    url, {})
                                    .subscribe(
                                        (response: Response) => {
                                            if (response['responseCode'] === 'CLI_SUC_11') {
                                                this.nextPage = response['responseData']['nextPage'];
                                                this.prePage = response['responseData']['prePage'];
                                                this.currPage = response['responseData']['currPage'];
                                                this.pages = response['responseData']['pages'];
                                                this.data = response['responseData']['data'];

                                                //console.log(response['responseData']);
                                            } else if (response['responseCode'] === 'CLI_ERR_11') {
                                                this.nextPage = null;
                                                this.prePage = null;
                                                this.currPage = 0;
                                                this.pages = [0];
                                                this.data = null;
                                            }
                                        },
                                        (error: any) => {
                                            this.apUtilServer.tokenExpired(error.json()['error']);
                                            //console.log(error.json())
                                        }
                                    );
                            }
                        }
                    },
                    (error: any) => {
                        this.apUtilServer.tokenExpired(error.json()['error']);
                        //console.log(error.json())
                    }
                );
        }
    }

    generateClientsPdfReport() {

        this.requestsService.getRequestReport(
            "/client/report/", {})
            .subscribe(
                data => {
                    //console.log(data);
                    var blob = new Blob([data], {type: 'application/pdf'});
                    FileSaver.saveAs(blob, "Client Detail Report.pdf");
                },
                (error: any) => {

                    this.apUtilServer.tokenExpired(error.json()['error']);
                    //console.log(error)
                }
            );
    }

    copyToClipboard() {
        if (copy(this.samplePassword)) {
            this.genericSuccess = "Sample password copied";
        } else {
            this.genericError = "System Error in copying of Sample password.";
        }
    }

    clientResetPassword(email: string) {
        this.requestsService.postRequest(
            '/client/auth/clientResetPasswordEmail',
            {
                'password': email
            }).subscribe(
            (response: Response) => {
                if (response['responseCode'] === 'CLI_PASS_SUC_04') {
                    this.genericSuccess = response['responseMessage'];
                } else {
                    this.genericError = response['responseMessage'];
                }
            },
            (error: any) => {
                //console.log(error.json())
                this.error = error.json();

            }
        );
    }

    resetFields() {
        this.newPassw = null;
        this.confirmPass = null;
    }

    onEditCustomerPopupLoad(selectedCustomer: any, index: number) {
        this.selectedCustomerId = selectedCustomer.id;
        this.selectedCustomerFirstName = selectedCustomer.firstName;
        this.selectedCustomerLastName = selectedCustomer.lastName;
        this.selectedCustomerIndex = index;
        this.editCustomerFirstName = selectedCustomer.firstName;
        this.editCustomerLastName = selectedCustomer.lastName;
        this.editCustomerPhoneNo = selectedCustomer.phoneNumber;
    }

    onEditCustomerPopupCancel(form: NgForm) {
        _.each(form.form.controls, function (control) {
            control['_touched'] = false;
        });
    }

    updateCustomer(form: NgForm) {
        this.isProcessing = true;
        _.each(form.form.controls, function (control) {
            control['_touched'] = true;
        });
        if (form.valid) {
            if (this.selectedCustomerId) {
                this.requestsService.putRequest(
                    '/client/update/' + this.selectedCustomerId
                    , {
                        "firstName": this.editCustomerFirstName,
                        "lastName": this.editCustomerLastName,
                        "phoneNumber": this.editCustomerPhoneNo,
                    })
                    .subscribe(
                        (response: Response) => {
                            if (response['responseCode'] === 'CLI_SUC_14') {
                                this.success = response['responseMessage'];
                                this.data[this.selectedCustomerIndex] = response['responseData'];
                                this.isProcessing = false;
                            } else {
                                this.error = response['responseMessage'];
                                this.isProcessing = false;
                            }
                        },
                        (error: any) => {
                            //console.log(error);
                            //this.genericError = error['responseMessage'];
                            this.apUtilServer.tokenExpired(error.json()['error']);
                            this.isProcessing = false;
                            this.error = error.json()['error'];
                        }
                    );
            }
        }
        this.isProcessing = false;
    }
}



