import {Component, OnInit} from '@angular/core';
import {RequestsService} from '../../services/requests.service';
import {Response} from '@angular/http';
import {Router} from '@angular/router';
import {APUtilService} from "../../services/ap.util.service";
import {AdminUpdateService} from "../../services/admin-update.service";
import * as FileSaver from 'file-saver';
import {Title} from '@angular/platform-browser';

@Component({
    selector: 'profile-component',
    template: require('../../templates/admin/profile.template.html'),
    styles: []
})
export class ProfileComponent implements OnInit {

    constructor(private requestsService: RequestsService,
                private router: Router,
                private apUtilServer: APUtilService,
                private adminUpdateService: AdminUpdateService,
                private titleService: Title) {
    }

    id: number;
    firstName: string;
    lastName: string;
    email: string;
    address: string;
    profileImg: string;
    userDesignation: string;
    phoneNumber: string;
    isActivated: Boolean;
    isLegitimatated: Boolean;
    isSuspended: Boolean;
    isLocked: Boolean;
    contactInfoEditMode: boolean;
    file: File;
    uploadMessageSuccess: string;
    uploadMessageError: string;
    isProfileImageUploading: boolean = false;
    editModeActivated: boolean = false;
    genericError: any;
    genericSuccess: any;

    ngOnInit() {
        if (window.localStorage.getItem('access_token')) {
            this.titleService.setTitle('My Profile');

            this.requestsService.getRequest(
                '/admin/'
                , {})
                .subscribe(
                    (response: Response) => {
                        if (response['responseCode'] === 'ADM_SUC_08') {
                            //console.log(response['responseData']);
                            this.id = response['responseData'].id;
                            this.firstName = response['responseData'].firstName;
                            this.lastName = response['responseData'].lastName;
                            this.email = response['responseData'].email;
                            this.address = response['responseData'].address + ' ' + response['responseData'].city + ' ' +
                                response['responseData'].state + ' ' + response['responseData'].country;
                            this.profileImg = response['responseData'].profileImg;
                            this.userDesignation = response['responseData'].role;
                            this.phoneNumber = response['responseData'].phoneNumber;
                            this.isActivated = response['responseData'].active;
                            this.isLegitimatated = response['responseData'].legitimate;
                            this.isSuspended = response['responseData'].blocked;
                            this.isLocked = response['responseData'].locked;

                        }
                    },
                    (error: any) => {
                        this.apUtilServer.tokenExpired(error.json()['error']);
                        //console.log(error.json())
                    }
                );
        } else {
            window.localStorage.removeItem('access_token');
            this.router.navigate(['/login']);
        }
    }

    editContactInformation(status: boolean) {
        this.contactInfoEditMode = status;
    }

    uploadProfileImg() {
        this.isProfileImageUploading = true;
        console.log(this.file.size);
        if (this.file.size <= 1048000) {
            this.requestsService.postRequestMultipartFormData(
                '/admin/uploadProfileImg/' + this.id
                , this.file)
                .subscribe(
                    (response: Response) => {
                        if (response['responseCode'] === 'ADM_SUC_15') {
                            this.uploadMessageSuccess = 'Profile Image uploaded successfully';
                            this.uploadMessageError = null;
                            this.isProfileImageUploading = false;

                            this.adminUpdateService.uploadProfileImage(response['responseData'].profileImgUrl + "?" + Math.floor(Math.random() * 50) + 1);
                            this.profileImg = response['responseData'].profileImgUrl + "?" + Math.floor(Math.random() * 50) + 1;
                        }
                    },
                    (error: any) => {
                        this.uploadMessageError = 'Profile Image uploading failed';
                        this.uploadMessageSuccess = null;
                        this.isProfileImageUploading = false;
                        this.apUtilServer.tokenExpired(error.json()['error']);
                        //console.log(error.json())
                    }
                );
        } else {
            this.uploadMessageError = "File size must be less then 1 kb.";
            this.uploadMessageSuccess = null;
            this.isProfileImageUploading = false;
        }
    }

    uploadProfileImgOnChange(event: any) {
        let fileList: FileList = event.target.files;
        if (fileList.length > 0) {
            this.file = fileList[0];
        }
    }

    onModelCancel() {
        this.uploadMessageError = null;
        this.uploadMessageSuccess = null;
    }

    editModeActivate(status: boolean) {
        this.editModeActivated = status;
    }

    updateAdministrator(adminId: number) {
        if (adminId) {
            this.requestsService.putRequest(
                '/admin/update/' + adminId
                , {
                    "firstName": this.firstName,
                    "lastName": this.lastName,
                    "phoneNumber": this.phoneNumber,
                })
                .subscribe(
                    (response: Response) => {
                        if (response['responseCode'] === 'ADM_SUC_14') {
                            this.genericSuccess = response['responseMessage'];
                            this.firstName = response['responseData'].firstName;
                            this.lastName = response['responseData'].lastName;
                            this.phoneNumber = response['responseData'].phoneNumber;
                            this.email = response['responseData'].email;
                            this.address = response['responseData'].address + ' ' + response['responseData'].city + ' ' +
                                response['responseData'].state + ' ' + response['responseData'].country;
                            this.editModeActivated = false;
                        } else {
                            this.genericError = response['responseMessage'];
                        }
                    },
                    (error: any) => {
                        //console.log(error);
                        //this.genericError = error['responseMessage'];
                        this.apUtilServer.tokenExpired(error.json()['error']);
                    }
                );
        }
    }

    generateAdminDetailReport() {
        this.requestsService.getRequestReport(
            "/admin/report/" + this.id, {})
            .subscribe(
                data => {
                    //console.log(data);
                    var blob = new Blob([data], {type: 'application/pdf'});
                    FileSaver.saveAs(blob, "Client Report - " + this.firstName + " " +
                        this.lastName + ".pdf"
                    )
                    ;
                },
                (error: any) => {
                    this.apUtilServer.tokenExpired(error.json()['error']);
                    //console.log(error)
                }
            );
    }
}



