import {Component, OnInit} from '@angular/core';
import {RequestsService} from '../../../services/requests.service';
import {Response} from '@angular/http';
import {Router} from '@angular/router';
import {NgForm} from "@angular/forms";
import * as _ from "lodash";
import {IMyDpOptions, MyDatePicker} from 'mydatepicker'
import {APUtilService} from "../../../services/ap.util.service";
import {PermissionsService} from "../../../services/permissions.service";
import * as copy from 'copy-to-clipboard';
import {Title} from "@angular/platform-browser";

@Component({
    selector: 'admin-component',
    template: require('../../../templates/dashboard/admin/admins.template.html'),
    styles: []
})
export class AdminComponent implements OnInit {

    constructor(private requestsService: RequestsService,
                private router: Router,
                private apUtilServer: APUtilService,
                private permissionsService: PermissionsService,
                private titleService: Title) {

    };

    private myDatePickerOptions: IMyDpOptions = {
        dateFormat: 'dd.mm.yyyy',
    };

    nextPage: any;
    prePage: any;
    currPage: any;
    pages: number[] = [];
    data: any;
    fullName: string = "";
    userName: string = "";
    emailAdd: string = "";
    adminId: number;
    newPassw: string;
    confirmPass: string;
    error: any;
    success: any;
    genericError: any;
    genericSuccess: any;
    dateFrom: MyDatePicker;
    dateTo: MyDatePicker;
    searchInputValue: string;

    dataArray: any[];
    minCharLen: string;
    minDigLen: string;
    minPassLen: string;
    maxPassLen: string;
    minLowerLen: string;
    minUpperLen: string;
    minSpecialCharLen: string;
    samplePassword: string;
    selectedAdminId: number;
    selectedAdminFirstName: string;
    selectedAdminLastName: string;
    selectedAdminIndex: number;
    editAdminFirstName: string;
    editAdminLastName: string;
    editAdminPhoneNo: string;

    ngOnInit() {
        if (window.localStorage.getItem('access_token')) {
            this.titleService.setTitle('Administrators');
            if (!this.permissionsService.hasPermission('manage_permissions')) {
                this.router.navigate(['/dashboard/404-not-found/']);
            }
            this.getAdminFromServer(0);
        }
    }

    getPageWiseAdmin(page: number) {
        this.getAdminFromServer(page);
        //console.log(page);
    }

    getAdminFromServer(page: number) {
        if (page > 0) {
            page = page;
        }
        this.requestsService.getRequest(
            '/admin/admins/' + page
            , {})
            .subscribe(
                (response: Response) => {
                    if (response['responseCode'] === 'ADM_SUC_09') {
                        this.nextPage = response['responseData']['nextPage'];
                        this.prePage = response['responseData']['prePage'];
                        this.currPage = response['responseData']['currPage'];
                        this.pages = response['responseData']['pages'];
                        this.data = response['responseData']['data']
                        //console.log(response['responseData']);
                    }
                },
                (error: any) => {
                    this.apUtilServer.tokenExpired(error.json()['error']);
                    //console.log(error.json()['error']);
                }
            );
    }

    refreshAdminsTable(page: number) {
        this.getAdminFromServer(page)
    }

    searchAdmins(form: NgForm) {
        _.each(form.form.controls, function (control) {
            control['_touched'] = true
        });

        var dateFromMilli = 0, dateToMilli = new Date().getTime();
        if (this.dateFrom != null) {
            dateFromMilli = this.dateFrom['epoc'] * 1000;
        }
        if (this.dateTo != null) {
            dateToMilli = this.dateTo['epoc'] + 86399;
            dateToMilli = dateToMilli * 1000;
        }

        this.requestsService.getRequest(
            '/admin/search/0?fullName=' + this.fullName + '&userName=' + this.userName +
            '&email=' + this.emailAdd + '&dateFrom=' + dateFromMilli + '&dateTo=' + dateToMilli
            , {})
            .subscribe(
                (response: Response) => {
                    if (response['responseCode'] === 'ADM_SUC_09') {
                        this.nextPage = response['responseData']['nextPage'];
                        this.prePage = response['responseData']['prePage'];
                        this.currPage = response['responseData']['currPage'];
                        this.pages = response['responseData']['pages'];
                        this.data = response['responseData']['data']

                        //console.log(response['responseData']);
                    } else if (response['responseCode'] === 'CLI_ERR_11') {
                        this.nextPage = null;
                        this.prePage = null;
                        this.currPage = 0;
                        this.pages = [0];
                        this.data = null;
                    }
                },
                (error: any) => {
                    this.apUtilServer.tokenExpired(error.json()['error']);
                    //console.log(error.json())
                }
            );
    }

    onResetPopupLoad(adminId: number) {
        this.adminId = adminId;
        this.error = null;
        this.success = null;
        this.newPassw = null;
        this.confirmPass = null;

        if (window.localStorage.getItem('access_token')) {

            this.requestsService.getRequest(
                '/admin/passwordPolicy/policies/' + adminId
                , {})
                .subscribe(
                    (response: Response) => {
                        if (response['responseCode'] === 'USR_POL_SUC_01') {
                            //console.log(response);
                            this.dataArray = response['responseData'];
                            for (var i = 0; i < this.dataArray.length; i++) {
                                if (this.dataArray[i].name === 'MIN_CHARACTER_LENGTH') {
                                    this.minCharLen = this.dataArray[i].userPolicy;

                                } else if (this.dataArray[i].name === 'MINIMUM_PASSWORD_LENGTH') {
                                    this.minPassLen = this.dataArray[i].userPolicy;

                                } else if (this.dataArray[i].name === 'MAXIMUM_PASSWORD_LENGTH') {
                                    this.maxPassLen = this.dataArray[i].userPolicy;

                                } else if (this.dataArray[i].name === 'MINIMUM_DIGITS') {
                                    this.minDigLen = this.dataArray[i].userPolicy;

                                } else if (this.dataArray[i].name === 'MINIMUM_LOWERCASE') {
                                    this.minLowerLen = this.dataArray[i].userPolicy;

                                } else if (this.dataArray[i].name === 'MINIMUM_UPPERCASE') {
                                    this.minUpperLen = this.dataArray[i].userPolicy;

                                } else if (this.dataArray[i].name === 'MINIMUM_SPECIAL_CHARACTER') {
                                    this.minSpecialCharLen = this.dataArray[i].userPolicy;

                                } else if (this.dataArray[i].name === 'SAMPLE_PASSWORD') {
                                    this.samplePassword = this.dataArray[i].userPolicy;

                                }
                            }
                        }
                    },
                    (error: any) => {
                        this.genericError = error;
                    }
                );
        }
    }

    onResetPopupCancel(form: NgForm) {
        _.each(form.form.controls, function (control) {
            control['_touched'] = false;
        })
    }

    resetPassword(form: NgForm) {
        _.each(form.form.controls, function (control) {
            control['_touched'] = true;
        });
        if (form.valid) {
            if (this.newPassw === this.confirmPass) {
                this.requestsService.putRequest(
                    '/admin/auth/resetPassword'
                    , {
                        'adminId': this.adminId,
                        'newPassword': this.newPassw,
                        'confirmPassword': this.confirmPass,
                    })
                    .subscribe(
                        (response: Response) => {
                            if (response['responseCode'] === 'ADMIN_PASS_SUC_01') {
                                this.error = null;
                                this.success = response['responseMessage'];
                            } else {
                                this.error = response['responseData'];
                                this.success = null;
                            }
                        },
                        (error: any) => {
                            //console.log(error.json())
                            this.error = error.json()['responseMessage'];
                            this.apUtilServer.tokenExpired(error.json()['error']);
                        }
                    );
            } else {
                this.error = 'New & Confirm password does not matched';
            }
        }
    }

    suspendAdmin(adminId: number, index: number) {

        if (adminId) {
            this.requestsService.putRequest(
                '/admin/suspend/' + adminId
                , {})
                .subscribe(
                    (response: Response) => {
                        if (response['responseCode'] === 'ADM_SUC_12') {
                            this.genericSuccess = response['responseMessage'];
                            this.data[index] = response['responseData'];
                            this.getAdminFromServer(this.currPage);
                        } else {
                            this.genericError = response['responseMessage'];
                        }
                    },
                    (error: any) => {
                        //console.log(error.json())
                        this.genericError = error.json()['responseMessage'];
                        this.apUtilServer.tokenExpired(error.json()['error']);
                    }
                );
        }
    }

    unsuspendAdmin(clientId: number, index: number) {

        if (clientId) {
            this.requestsService.putRequest(
                '/admin/unSuspend/' + clientId
                , {})
                .subscribe(
                    (response: Response) => {
                        if (response['responseCode'] === 'ADM_SUC_13') {
                            this.genericSuccess = response['responseMessage'];
                            this.data[index] = response['responseData'];
                        } else {
                            this.genericError = response['responseMessage'];
                        }
                    },
                    (error: any) => {
                        //console.log(error.json())
                        this.genericError = error.json()['responseMessage'];
                        this.apUtilServer.tokenExpired(error.json()['error']);
                    }
                );
        }
    }

    onEnterSearch() {
        var regexp = new RegExp(/^\w+@[a-zA-Z_]+?\.[a-zA-Z]{2,3}$/);
        var emailAdd = "", url;
        var searchInputValueEmail = regexp.test(this.searchInputValue);

        if (searchInputValueEmail === true) {
            emailAdd = this.searchInputValue;
            url = '/admin/search/0?fullName=' + '' + '&userName=' + '' +
                '&email=' + emailAdd + '&dateFrom=' + 0 + '&dateTo=' + new Date().getTime()

            this.requestsService.getRequest(
                url, {})
                .subscribe(
                    (response: Response) => {
                        if (response['responseCode'] === 'ADM_SUC_09') {
                            this.nextPage = response['responseData']['nextPage'];
                            this.prePage = response['responseData']['prePage'];
                            this.currPage = response['responseData']['currPage'];
                            this.pages = response['responseData']['pages'];
                            this.data = response['responseData']['data'];

                            //console.log(response['responseData']);
                        } else if (response['responseCode'] === 'ADM_ERR_03') {
                            this.nextPage = null;
                            this.prePage = null;
                            this.currPage = 0;
                            this.pages = [0];
                            this.data = null;
                        }
                    },
                    (error: any) => {
                        this.apUtilServer.tokenExpired(error.json()['error']);
                        //console.log(error.json())
                    }
                );

        } else {
            url = '/admin/search/0?fullName=' + this.searchInputValue + '&userName=' + '' +
                '&email=' + '' + '&dateFrom=' + 0 + '&dateTo=' + new Date().getTime()

            this.requestsService.getRequest(
                url, {})
                .subscribe(
                    (response: Response) => {
                        if (response['responseCode'] === 'ADM_SUC_09') {
                            this.nextPage = response['responseData']['nextPage'];
                            this.prePage = response['responseData']['prePage'];
                            this.currPage = response['responseData']['currPage'];
                            this.pages = response['responseData']['pages'];
                            this.data = response['responseData']['data'];

                            //console.log(response['responseData']);
                        } else if (response['responseCode'] === 'ADM_ERR_03') {
                            this.nextPage = null;
                            this.prePage = null;
                            this.currPage = 0;
                            this.pages = [0];
                            this.data = null;

                            if (!this.data) {
                                url = '/admin/search/0?fullName=' + '' + '&userName=' + this.searchInputValue +
                                    '&email=' + '' + '&dateFrom=' + 0 + '&dateTo=' + new Date().getTime()
                                this.requestsService.getRequest(
                                    url, {})
                                    .subscribe(
                                        (response: Response) => {
                                            if (response['responseCode'] === 'ADM_SUC_09') {
                                                this.nextPage = response['responseData']['nextPage'];
                                                this.prePage = response['responseData']['prePage'];
                                                this.currPage = response['responseData']['currPage'];
                                                this.pages = response['responseData']['pages'];
                                                this.data = response['responseData']['data'];

                                                //console.log(response['responseData']);
                                            } else if (response['responseCode'] === 'ADM_ERR_03') {
                                                this.nextPage = null;
                                                this.prePage = null;
                                                this.currPage = 0;
                                                this.pages = [0];
                                                this.data = null;
                                            }
                                        },
                                        (error: any) => {
                                            this.apUtilServer.tokenExpired(error.json()['error']);
                                            //console.log(error.json())
                                        }
                                    );
                            }
                        }
                    },
                    (error: any) => {
                        this.apUtilServer.tokenExpired(error.json()['error']);
                        //console.log(error.json())
                    }
                );
        }
    }

    copyToClipboard() {
        if (copy(this.samplePassword)) {
            this.genericSuccess = "Sample password copied";
        } else {
            this.genericError = "System Error in copying of Sample password.";
        }
    }

    onEditAdminPopupLoad(selectedAdmin: any, index: number) {
        this.error = null;
        this.success = null;
        this.selectedAdminId = selectedAdmin.id;
        this.selectedAdminFirstName = selectedAdmin.firstName;
        this.selectedAdminLastName = selectedAdmin.lastName;
        this.selectedAdminIndex = index;
        this.editAdminFirstName = selectedAdmin.firstName;
        this.editAdminLastName = selectedAdmin.lastName;
        this.editAdminPhoneNo = selectedAdmin.phoneNumber;
    }

    onEditAdminPopupCancel(form: NgForm) {
        _.each(form.form.controls, function (control) {
            control['_touched'] = false;
        });
    }

    updateAdmin(form: NgForm) {
        _.each(form.form.controls, function (control) {
            control['_touched'] = true;
        });
        if (form.valid) {
            if (this.selectedAdminId) {
                this.requestsService.putRequest(
                    '/admin/update/' + this.selectedAdminId
                    , {
                        "firstName": this.editAdminFirstName,
                        "lastName": this.editAdminLastName,
                        "phoneNumber": this.editAdminPhoneNo,
                    })
                    .subscribe(
                        (response: Response) => {
                            if (response['responseCode'] === 'ADM_SUC_14') {
                                //console.log(this.selectedAdminIndex);
                                //Cheapy, Popup is not being hide for 0 index
                                if (this.selectedAdminIndex != 0) {
                                    this.data[this.selectedAdminIndex] = response['responseData'];
                                }
                                this.success = response['responseMessage'];
                                //console.log(this.selectedAdminIndex);
                            } else {
                                this.error = response['responseMessage'];
                            }
                            return;
                        },
                        (error: any) => {
                            //console.log(error);
                            //this.genericError = error['responseMessage'];
                            this.apUtilServer.tokenExpired(error.json()['error']);
                            this.error = error.json()['error'];
                        }
                    );
            }
        }
    }
}



