import {Component, OnInit} from '@angular/core';
import {RequestsService} from '../../../services/requests.service';
import {Router, ActivatedRoute} from '@angular/router';
import {APUtilService} from "../../../services/ap.util.service";
import {AppConfig} from "../../../configuration/app.config";
import * as FileSaver from 'file-saver';
import {Title} from "@angular/platform-browser";

@Component({
    selector: 'customer-detail-component',
    template: require('../../../templates/dashboard/customer/customer-detail.template.html'),
    styles: []
})
export class CustomerDetailComponent implements OnInit {

    constructor(private requestsService: RequestsService,
                private router: Router,
                private activatedRoute: ActivatedRoute,
                private apUtilServer: APUtilService,
                private titleService: Title) {
    };

    id: number;
    userName: string;
    firstName: string;
    lastName: string;
    phoneNumber: string;
    email: string;
    address: string;
    loggedInCount: number = 0;
    totalDeviceCount: number = 0;
    ownedDeviceCount: number = 0;
    genericError: any;
    genericSuccess: any;
    editModeActivated: boolean = false;
    paymentPlan: string;
    createdMembers: string;
    totalMembers: string;
    currency: string;
    totalPaid: string;
    estMonthlyBill: string;
    contractExpires: string;
    packageStatus: string;
    packageTerm: string;
    billingConnected: boolean = false;
    consoleConnected: boolean = false;
    deviceInfoConsoleConnected: boolean = false;
    legitimate: boolean = false;
    billingToken: string;
    subscriptionId: string;
    amendmentCode: string;
    amendmentDate: string;
    billingPeriod: string;
    totalSubsCharges: string;
    balanceAmount: string;
    creditBalance: string;
    initialTerm: string;
    renewalTerm: string;
    triggerCondition: string;
    subsChargesCalBase: string;
    isProcessing: boolean = false;

    ngOnInit() {
        if (window.localStorage.getItem('access_token')) {

            this.activatedRoute.params.subscribe(
                params => {
                    this.id = params['id'];
                    this.requestsService.getRequest(
                        '/client/' + this.id, {}
                    ).subscribe(
                        response => {
                            //console.log(response);
                            if (response['responseCode'] === 'CLI_SUC_08') {
                                this.firstName = response['responseData'].firstName;
                                this.lastName = response['responseData'].lastName;
                                this.phoneNumber = response['responseData'].phoneNumber;
                                this.email = response['responseData'].email;
                                this.address = response['responseData'].address + ' ' + response['responseData'].city + ' ' +
                                    response['responseData'].state + ' ' + response['responseData'].country;
                                this.loggedInCount = response['responseData'].loggedInCount;
                                this.legitimate = response['responseData'].legitimate;
                                this.totalDeviceCount = response['responseData'].devicesLoggedInCount;

                                this.titleService.setTitle(this.firstName + ' ' + this.lastName + ' - Dashboard');

                                this.requestsService.getTokenFromBilling(
                                    '/auth/signIn'
                                    , {
                                        'username': 'admin',
                                        'password': 'admin',
                                    })
                                    .subscribe(
                                        (response: Response) => {
                                            //console.log(response);
                                            if (response['token']) {
                                                this.billingConnected = true;
                                                var token = response['token'];
                                                //console.log(token);
                                                this.requestsService.getRequestToBilling(
                                                    '/customerSubscription/get?email=' + this.email
                                                    , {}
                                                    , token)
                                                    .subscribe(
                                                        (response: Response) => {
                                                            if (response['status']['name'] === 'OK') {
                                                                this.paymentPlan = response['customerSubscription'].packageName;
                                                                this.createdMembers = 'Billing doest support';
                                                                this.totalMembers = 'Billing doest support';
                                                                this.currency = response['customerSubscription'].currency;
                                                                this.totalPaid = 'Billing doest support';
                                                                this.estMonthlyBill = response['customerSubscription'].monthlyBill;
                                                                this.contractExpires = response['customerSubscription'].contractExpire;
                                                                this.packageStatus = response['customerSubscription'].packageStatus;
                                                                this.packageTerm = response['customerSubscription'].paymentTerm;
                                                                this.balanceAmount = response['customerSubscription'].balance;
                                                                this.creditBalance = response['customerSubscription'].creditBalance;
                                                                this.initialTerm = response['customerSubscription'].initialTerm;
                                                                this.renewalTerm = response['customerSubscription'].renewalTerm;
                                                                this.triggerCondition = response['customerSubscription'].triggerCondition;
                                                                this.subsChargesCalBase = response['customerSubscription'].subscriptionCharges;
                                                                this.amendmentCode = response['customerSubscription'].amendmentId;
                                                                this.amendmentDate = response['customerSubscription'].amendmentDate;
                                                                this.billingPeriod = response['customerSubscription'].creditBalance;
                                                                this.totalSubsCharges = response['customerSubscription'].billingPeriod;

                                                            } else {
                                                                this.billingConnected = false;
                                                                this.genericError = "Customer subscription not found.";
                                                            }
                                                        },
                                                        (error: any) => {
                                                            //console.log(error.json())
                                                            this.billingConnected = false;
                                                            this.genericError = "Billing down or Authorization failed";
                                                        }
                                                    );

                                            } else {
                                                this.billingConnected = false;
                                                this.genericError = "Billing down or Authorization failed";
                                            }
                                        },
                                        (error: any) => {
                                            this.billingConnected = false;
                                            this.genericError = "Billing down or Authorization failed";
                                        }
                                    );

                                this.requestsService.getTokenFromConsole(
                                    '/auth/signIn'
                                    //#TODO need to dynamic as per customer created
                                    , {
                                        'username': AppConfig.CONSOLE_TOKEN_USERNAME,
                                        'password': AppConfig.CONSOLE_TOKEN_PASSWORD,
                                    })
                                    .subscribe(
                                        (response: Response) => {
                                            //console.log(response);
                                            if (response['token']) {
                                                var consoleToken = response['token'];
                                                this.deviceInfoConsoleConnected = true;
                                                this.requestsService.postRequestToConsole(
                                                    '/device/deviceCount'
                                                    , {
                                                        'userId': this.email
                                                    },
                                                    consoleToken
                                                ).subscribe(
                                                    (responseDevices: Response) => {
                                                        this.ownedDeviceCount = responseDevices['deviceCount'];
                                                    },
                                                    (error: any) => {
                                                        this.genericError = error;
                                                        this.deviceInfoConsoleConnected = true;
                                                    }
                                                );
                                            } else {
                                                this.deviceInfoConsoleConnected = true;
                                            }
                                        },
                                        (error: any) => {
                                            //console.log(error.json())
                                            this.deviceInfoConsoleConnected = true;
                                            this.genericError = "Authorization failed from Console";
                                        }
                                    );
                            } else if (response['responseCode'] === 'CLI_ERR_08') {
                                this.router.navigate(['/dashboard/customer/404-not-found']);
                            }
                        },
                        (error: any) => {
                            this.apUtilServer.tokenExpired(error.json()['error']);
                            this.router.navigate(['/login/']);
                        });
                });
            this.titleService.setTitle('Customer Dashboard');
        }
    }

    updateCustomer(clientId: number) {
        if (clientId) {
            this.requestsService.putRequest(
                '/client/update/' + clientId
                , {
                    "firstName": this.firstName,
                    "lastName": this.lastName,
                    "phoneNumber": this.phoneNumber,
                })
                .subscribe(
                    (response: Response) => {
                        if (response['responseCode'] === 'CLI_SUC_14') {
                            this.genericSuccess = response['responseMessage'];
                            this.firstName = response['responseData'].firstName;
                            this.lastName = response['responseData'].lastName;
                            this.phoneNumber = response['responseData'].phoneNumber;
                            this.email = response['responseData'].email;
                            this.address = response['responseData'].address + ' ' + response['responseData'].city + ' ' +
                                response['responseData'].state + ' ' + response['responseData'].country;
                            this.editModeActivated = false;
                        } else {
                            this.genericError = response['responseMessage'];
                        }
                    },
                    (error: any) => {
                        //console.log(error);
                        //this.genericError = error['responseMessage'];
                        this.apUtilServer.tokenExpired(error.json()['error']);
                    }
                );
        }
    }

    editModeActivate(status: boolean) {
        this.editModeActivated = status;
    }

    goToCustomerConsole() {
        this.isProcessing = true;
        if (this.legitimate) {
            this.requestsService.getTokenFromConsole(
                '/auth/signIn'
                //#TODO need to dynamic as per customer created
                , {
                    'username': AppConfig.CONSOLE_TOKEN_USERNAME,
                    'password': AppConfig.CONSOLE_TOKEN_PASSWORD,
                })
                .subscribe(
                    (response: Response) => {
                        //console.log(response);
                        if (response['token']) {
                            this.consoleConnected = true;
                            window.location.href = AppConfig.CONSOLE_API_ENDPOINT + '#/dashboard/stc/:' + this.email + '/:' + response['token'];
                        } else {
                            this.consoleConnected = false;
                        }
                    },
                    (error: any) => {
                        //console.log(error.json())
                        this.consoleConnected = false;
                        this.genericError = "Authorization failed from Console";
                    }
                );
        } else {
            this.genericError = "Customer not legitimated";
        }
        this.isProcessing = false;
    }

    generateClientDetailReport() {
        this.isProcessing = true;
        this.requestsService.getRequestReport(
            "/client/report/" + this.id, {})
            .subscribe(
                data => {
                    //console.log(data);
                    var blob = new Blob([data], {type: 'application/pdf'});
                    FileSaver.saveAs(blob, "Client Report - " + this.firstName + " " +
                        this.lastName + ".pdf"
                    )
                    ;
                },
                (error: any) => {
                    this.apUtilServer.tokenExpired(error.json()['error']);
                    //console.log(error)
                }
            );
        this.isProcessing = false;
    }

    getTokenFromBilling() {
        this.requestsService.getTokenFromBilling(
            '/auth/signIn'
            , {
                'username': 'admin',
                'password': 'admin',
            })
            .subscribe(
                (response: Response) => {
                    //console.log(response);
                    if (response['token']) {
                        this.billingToken = response['token'];
                    }
                },
                (error: any) => {
                    this.billingToken = null;
                }
            );
    }

    generateClientLogReport() {
        this.isProcessing = true;
        this.requestsService.getRequestReport(
            "/client/report/loginLogoutReport/" + this.id, {})
            .subscribe(
                data => {
                    //console.log(data);
                    var blob = new Blob([data], {type: 'application/pdf'});
                    FileSaver.saveAs(blob, "Client Login-Logout Report - " + this.firstName + " " +
                        this.lastName + ".pdf"
                    );
                },
                (error: any) => {
                    this.apUtilServer.tokenExpired(error.json()['error']);
                    //console.log(error)
                }
            );
        this.isProcessing = false;
    }

    goToActivityLogs(clientId: number) {
        if (window.localStorage.getItem('access_token')) {
            var route = '/dashboard/customer/' + clientId + '/activityLogs'
            this.router.navigate([route]);
        }
    }
}



