import { Component, OnInit } from '@angular/core';
import { Response } from '@angular/http';
import { NgForm } from '@angular/forms';
import { Router, ActivatedRoute } from '@angular/router';
import { WindowService } from '../../../services/window.service';
import { RequestsService } from '../../../services/requests.service';
import * as _ from 'lodash';

@Component({
  selector: 'role-edit-component',
  template: require('../../../templates/dashboard/role/role-add-edit.template.html'),
  styles: [`
    .help-text{
      color: #777;
      font-size: 11px;
      line-height: 30px;

    }
    .help-text-bottom{
      color: #777;
      font-size: 11px;
      line-height: 10px;
      width: 500px;
      z-index: 333
    }
  `],
  providers: [ WindowService, RequestsService ]
})
export class RoleEditComponent implements OnInit{
  constructor (
    private window: WindowService,
    private requestsService: RequestsService,
    private router: Router,
    private activatedRoute: ActivatedRoute){
  };
  title: string = 'Edit Role('
  id: any;
  roleName: string;

  onCapabilityChange() {
    console.log('Handle Role Change');
  }

  saveRole(form: NgForm){
    _.each(form.form.controls, function(control:any){control['_touched']=true});
    if (form.valid){
      this.requestsService.postRequest(
        '/auth/role/update/' + this.id + '/'
        ,{
          "name": this.roleName,
        }
      ).subscribe(
        (response: Response) => {
          this.router.navigate(['/dashboard/role/list/']);
        },
        (error: any) => console.log(error),
      );
    }
  }

  cancel(){
    this.router.navigate(['/dashboard/role/list']);
  }
  
  ngOnInit(){

  }
}


