import {Component, OnInit} from '@angular/core';
import {RequestsService} from '../../services/requests.service';
import {Response} from '@angular/http';
import {Router} from '@angular/router';
import {APUtilService} from "../../services/ap.util.service";
import {Title} from '@angular/platform-browser';

@Component({
    selector: 'activity-log-component',
    template: require('../../templates/admin/activity-log.template.html'),
    styles: []
})
export class ActivityLogComponent implements OnInit {

    constructor(private requestsService: RequestsService,
                private apUtilServer: APUtilService,
                private titleService: Title) {
    }

    id: number;
    nextPage: any;
    prePage: any;
    currPage: any;
    pages: number[] = [];
    data: any;

    ngOnInit() {
        if (window.localStorage.getItem('access_token')) {
            this.titleService.setTitle('My Activity Log');
            this.getActivityLogsFromServer(0);
        }
    }

    getPageWiseActivityLogs(page: number) {
        this.getActivityLogsFromServer(page);
        //console.log(page);
    }

    getActivityLogsFromServer(page: number) {
        if (page > 0) {
            page = page;
        }
        this.requestsService.getRequest(
            '/admin/activitylog/' + 0 + "/" + page
            , {})
            .subscribe(
                (response: Response) => {
                    if (response['responseCode'] === 'ADM_AL_SUC_01') {
                        this.nextPage = response['responseData']['nextPage'];
                        this.prePage = response['responseData']['prePage'];
                        this.currPage = response['responseData']['currPage'];
                        this.pages = response['responseData']['pages'];
                        this.data = response['responseData']['data'];

                        console.log(response['responseData']);
                    }
                },
                (error: any) => {
                    this.apUtilServer.tokenExpired(error.json()['error']);
                    //console.log(error.json()['error']);
                }
            );
    }

    refreshActivityLogs(page: number) {
        this.getActivityLogsFromServer(page)
    }
}



