import {Component} from "@angular/core";
import {NgForm} from "@angular/forms";
import {Response} from "@angular/http";
import {RequestsService} from "../services/requests.service";
import {Title} from "@angular/platform-browser";

@Component({
    selector: 'forgot-password-component',
    template: require('../templates/forgot-password.template.html'),
    styles: [],
})
export class ForgotPasswordComponent {
    constructor(private requestsService: RequestsService,
                private titleService: Title) {
    };

    username: string;
    success: any;
    error: any;

    ngOnInit() {
        this.titleService.setTitle('Forgot Password');
    }

    onChange() {
        this.error = null;
    }

    forgotPassword(form: NgForm) {
        this.requestsService.postUnAuthRequest(
            '/unAuth/admin/forgotPassword?loginName=' + this.username
            , {})
            .subscribe(
                (response: Response) => {
                    if (response['responseCode'] === 'ADM_SUC_10') {
                        this.success = response['responseMessage'];
                    } else {
                        this.error = response['responseMessage'];
                    }
                },
                (error: any) => {
                    //console.log(error.json())
                    this.error = error.json();
                }
            );
    }
}



