import {Component, OnInit} from '@angular/core';
import {RequestsService} from '../../services/requests.service';
import {Response} from '@angular/http';
import {Router} from '@angular/router';
import {APUtilService} from "../../services/ap.util.service";
import {NgForm} from "@angular/forms";
import * as _ from "lodash";
import * as copy from 'copy-to-clipboard';
import {Title} from '@angular/platform-browser';

@Component({
    selector: 'profile-component',
    template: require('../../templates/admin/settings.template.html'),
    styles: []
})
export class SettingsComponent implements OnInit {

    constructor(private requestsService: RequestsService,
                private router: Router,
                private apUtilServer: APUtilService,
                private titleService: Title) {
    }

    id: number;
    firstName: string;
    lastName: string;
    profileImg: string;
    userDesignation: string;
    currentPassword: string;
    newPassword: string;
    reTypePassword: string;
    uploadMessageSuccess: string;
    uploadMessageError: string;
    genericError: any;
    genericSuccess: any;
    dataArray: any[];
    minCharLen: string;
    minDigLen: string;
    minPassLen: string;
    maxPassLen: string;
    minLowerLen: string;
    minUpperLen: string;
    minSpecialCharLen: string;
    samplePassword: string;

    ngOnInit() {
        if (window.localStorage.getItem('access_token')) {
            this.titleService.setTitle('My Settings');

            this.requestsService.getRequest(
                '/admin/'
                , {})
                .subscribe(
                    (response: Response) => {
                        if (response['responseCode'] === 'ADM_SUC_08') {
                            //console.log(response['responseData']);
                            this.id = response['responseData'].id;
                            this.firstName = response['responseData'].firstName;
                            this.lastName = response['responseData'].lastName;
                            this.profileImg = response['responseData'].profileImg;
                            this.userDesignation = response['responseData'].role;

                            this.requestsService.getRequest(
                                '/admin/passwordPolicy/policies/' + this.id
                                , {})
                                .subscribe(
                                    (response: Response) => {
                                        if (response['responseCode'] === 'USR_POL_SUC_01') {
                                            //console.log(response);
                                            this.dataArray = response['responseData'];
                                            for (var i = 0; i < this.dataArray.length; i++) {
                                                if (this.dataArray[i].name === 'MIN_CHARACTER_LENGTH') {
                                                    this.minCharLen = this.dataArray[i].userPolicy;

                                                } else if (this.dataArray[i].name === 'MINIMUM_PASSWORD_LENGTH') {
                                                    this.minPassLen = this.dataArray[i].userPolicy;

                                                } else if (this.dataArray[i].name === 'MAXIMUM_PASSWORD_LENGTH') {
                                                    this.maxPassLen = this.dataArray[i].userPolicy;

                                                } else if (this.dataArray[i].name === 'MINIMUM_DIGITS') {
                                                    this.minDigLen = this.dataArray[i].userPolicy;

                                                } else if (this.dataArray[i].name === 'MINIMUM_LOWERCASE') {
                                                    this.minLowerLen = this.dataArray[i].userPolicy;

                                                } else if (this.dataArray[i].name === 'MINIMUM_UPPERCASE') {
                                                    this.minUpperLen = this.dataArray[i].userPolicy;

                                                } else if (this.dataArray[i].name === 'MINIMUM_SPECIAL_CHARACTER') {
                                                    this.minSpecialCharLen = this.dataArray[i].userPolicy;

                                                } else if (this.dataArray[i].name === 'SAMPLE_PASSWORD') {
                                                    this.samplePassword = this.dataArray[i].userPolicy;

                                                }
                                            }
                                        }
                                    },
                                    (error: any) => {
                                        this.genericError = error;
                                    }
                                );
                        }
                    },
                    (error: any) => {
                        this.apUtilServer.tokenExpired(error.json()['error']);
                        //console.log(error.json())
                    }
                );
        } else {
            window.localStorage.removeItem('access_token');
            this.router.navigate(['/login']);
        }
    }

    updateAdminPassword(adminId: number, form: NgForm) {
        _.each(form.form.controls, function (control) {
            control['_touched'] = true
        })

        if (form.valid) {
            this.genericSuccess = null;
            this.genericError = null;
            if (adminId) {
                if (this.newPassword != this.reTypePassword) {
                    this.genericError = "New Password and Re-Type Password don't matched.";
                    return;
                }

                this.requestsService.putRequest(
                    '/admin/auth/changePassword'
                    , {
                        "adminId": adminId,
                        "currPassword": this.currentPassword,
                        "newPassword": this.newPassword,
                        "reTypePassword": this.reTypePassword,
                    })
                    .subscribe(
                        (response: Response) => {
                            if (response['responseCode'] === 'ADM_PASS_SUC_02') {
                                this.genericSuccess = response['responseMessage'];
                            } else {
                                this.genericError = response['responseMessage'];
                            }
                        },
                        (error: any) => {
                            //console.log(error);
                            this.apUtilServer.tokenExpired(error.json()['error']);
                        }
                    );
            }
        }
    }

    copyToClipboard() {
        if (copy(this.samplePassword)) {
            this.genericSuccess = "Sample password copied";
        } else {
            this.genericError = "System Error in copying of Sample password.";
        }
    }
}



