import {Component, OnInit} from '@angular/core';
import {RequestsService} from '../../../services/requests.service';
import {Router, ActivatedRoute} from '@angular/router';
import {APUtilService} from "../../../services/ap.util.service";
import {Title} from "@angular/platform-browser";

@Component({
    selector: 'admin-detail-component',
    template: require('../../../templates/dashboard/admin/admin-detail.template.html'),
    styles: []
})
export class AdminDetailComponent implements OnInit {

    constructor(private requestsService: RequestsService,
                private router: Router,
                private activatedRoute: ActivatedRoute,
                private apUtilServer: APUtilService,
                private titleService: Title) {
    };

    id: number;
    userName: string;
    firstName: string;
    lastName: string;
    phoneNumber: string;
    email: string;
    address: string;
    loggedInCount: number = 0;
    genericError: any;
    genericSuccess: any;
    editModeActivated: boolean = false;
    paymentPlan: string;
    createdMembers: string;
    totalMembers: string;
    currency: string;
    totalPaid: string;
    estMonthlyBill: string;
    contractExpires: string;
    packageStatus: string;
    packageTerm: string;
    billingConnected: boolean = false;
    consoleConnected: boolean = false;

    ngOnInit() {
        if (window.localStorage.getItem('access_token')) {

            this.activatedRoute.params.subscribe(
                params => {
                    this.id = params['id'];
                    this.requestsService.getRequest(
                        '/admin/' + this.id, {}
                    ).subscribe(
                        response => {
                            //console.log(response);
                            if (response['responseCode'] === 'ADM_SUC_08') {
                                this.firstName = response['responseData']['contact'].firstName;
                                this.lastName = response['responseData']['contact'].lastName;
                                this.phoneNumber = response['responseData']['contact'].phoneNumber;
                                this.email = response['responseData'].email;
                                this.address = response['responseData']['contact'].address + ' ' + response['responseData']['contact'].city + ' ' +
                                    response['responseData']['contact'].state + ' ' + response['responseData']['contact'].country;
                                this.loggedInCount = response['responseData']['contact'].loggedInCount;

                                this.titleService.setTitle(this.firstName + ' ' + this.lastName + ' - Dashboard');
                            } else if (response['responseCode'] === 'ADM_ERR_01') {
                                this.router.navigate(['/dashboard/customer/404-not-found']);
                            }

                        },
                        (error: any) => {
                            this.apUtilServer.tokenExpired(error.json()['error']);
                            this.router.navigate(['/login/']);
                        });
                });
            if (!this.titleService.getTitle()) {
                this.titleService.setTitle('Admin Dashboard');
            }
        }
    }

    updateAdmin(adminId: number) {
        if (adminId) {
            this.requestsService.putRequest(
                '/admin/update/' + adminId
                , {
                    "firstName": this.firstName,
                    "lastName": this.lastName,
                    "phoneNumber": this.phoneNumber,
                })
                .subscribe(
                    (response: Response) => {
                        if (response['responseCode'] === 'ADM_SUC_14') {
                            this.genericSuccess = response['responseMessage'];
                            this.firstName = response['responseData'].firstName;
                            this.lastName = response['responseData'].lastName;
                            this.phoneNumber = response['responseData'].phoneNumber;
                            this.email = response['responseData'].email;
                            this.address = response['responseData'].address + ' ' + response['responseData'].city + ' ' +
                                response['responseData'].state + ' ' + response['responseData'].country;
                            this.editModeActivated = false;
                        } else {
                            this.genericError = response['responseMessage'];
                        }
                    },
                    (error: any) => {
                        console.log(error);
                        //this.genericError = error['responseMessage'];
                        this.apUtilServer.tokenExpired(error.json()['error']);
                    }
                );

        }
    }

    editModeActivate(status: boolean) {
        this.editModeActivated = status;
    }
}



